
.header__wrapper{
  
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 0;
}
.header__info{
  display: flex;
  align-items: center;
}
.header__info-text{
  font-size: 18px;
  max-width: 150px;
  position: relative;
  color: #303030;
  font-size: 21px;
  font-weight: 700;
  img{
    position: absolute;
    top: -15px;
    z-index: -1;
  }
  span{
    color: #9789aa;
  }
}
.btn{
  
  
  color: #303030;
  font-size: 19px;
  font-weight: 700;
  border-radius: 30px;
  border: 2px solid #000;
  padding: 10px 30px;
  cursor: pointer;
  transition: all .2s linear;
  margin-left: 60px;
  &:hover{
    background: #303030;
    color:#fff;
    border: 2px solid transparent;
  }
}
.header__menu{
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  li{
    text-transform: capitalize;
    color: #303030;
    font-size: 18px;
  }
}
